import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { CgSearch } from 'react-icons/cg';

function Home() {
  const key = 'AIzaSyB76xdQCyoeQV2bqh56AcxhYF_HS_vAUt4';

  const [book, setBook] = useState('');
  const [result, setResult] = useState([]);

  function handleChange(event) {
    const book = event.target.value;
    setBook(book);
  }

  let api_link =
    'https://www.googleapis.com/books/v1/volumes?q=' +
    book +
    '&key=' +
    key +
    '&maxResults=40';

  function handleSubmit(event) {
    event.preventDefault();
    axios.get(api_link).then((data) => {
      console.log(data.data.items);
      setResult(data.data.items);
    });
  }

  function check(image) {
    try {
      return image.thumbnail;
    } catch (err) {
      return 'no_cover_thumb.gif';
    }
  }

  return (
    <div className='book-search'>
      <h1>Search over 10 million books...</h1>
      <form onSubmit={handleSubmit}>
        <input
          onChange={handleChange}
          type='text'
          placeholder='Search for google books'
          autoComplete='off'
        />
        <button type='submit'>Search</button>
      </form>

      <div className='grid-container'>
        {result.map((book) => (
          <div className='book'>
            <a
              href={book.volumeInfo.previewLink}
              className='bookPlaceholder'
              target='_blank'
            >
              <img
                src={check(book.volumeInfo.imageLinks)}
                alt={book.volumeInfo.title}
              />
              <div className='bookInfo'>
                <p className='title'>{book.volumeInfo.title}</p>
                <p className='author'>
                  by <b>{book.volumeInfo.authors}</b>
                </p>
              </div>
            </a>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Home;
